/**
 * Contexte d'authentification pour l'application
 * Fournit des fonctionnalits d'authentification  l'ensemble de l'application
 */

import React, { createContext, useState, useEffect, useContext, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import enhancedAuthClient from '../services/enhancedAuthClient';
import AuthErrorHandler from '../components/auth/AuthErrorHandler';

// Cration du contexte
const AuthContext = createContext(null);

/**
 * Fournisseur du contexte d'authentification
 * @param {Object} props Props du composant
 * @param {React.ReactNode} props.children Enfants du composant
 */
export const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  // Fonction pour charger le profil utilisateur
  const loadUserProfile = useCallback(async () => {
    try {
      const userInfo = await enhancedAuthClient.getUserInfo();
      setCurrentUser(userInfo);
      return userInfo;
    } catch (err) {
      console.error('Erreur lors du chargement du profil utilisateur:', err);
      setCurrentUser(null);
      return null;
    }
  }, []);

  // Vrifier l'tat d'authentification au chargement
  useEffect(() => {
    const checkAuth = async () => {
      setLoading(true);
      try {
        const isAuthenticated = await enhancedAuthClient.isAuthenticated();
        
        if (isAuthenticated) {
          await loadUserProfile();
        } else {
          setCurrentUser(null);
        }
      } catch (err) {
        console.error('Erreur lors de la vrification de l\'authentification:', err);
        setCurrentUser(null);
      } finally {
        setLoading(false);
      }
    };

    checkAuth();
  }, [loadUserProfile]);

  // Grer les vnements d'authentification
  useEffect(() => {
    const handleSessionExpired = () => {
      setCurrentUser(null);
      setError({
        code: 'session_expired',
        message: 'Votre session a expir. Veuillez vous reconnecter.'
      });
    };

    const handleTokenRevoked = () => {
      setCurrentUser(null);
      setError({
        code: 'token_revoked',
        message: 'Votre session a t rvoque. Veuillez vous reconnecter.'
      });
    };

    const handleAuthError = (event) => {
      setError(event.detail);
    };

    // S'abonner aux vnements
    window.addEventListener('auth:session-expired', handleSessionExpired);
    window.addEventListener('auth:token-revoked', handleTokenRevoked);
    window.addEventListener('auth:error', handleAuthError);

    // Se dsabonner  la destruction
    return () => {
      window.removeEventListener('auth:session-expired', handleSessionExpired);
      window.removeEventListener('auth:token-revoked', handleTokenRevoked);
      window.removeEventListener('auth:error', handleAuthError);
    };
  }, [navigate]);

  // Fonction de connexion
  const login = async (credentials) => {
    setLoading(true);
    try {
      const user = await enhancedAuthClient.login(credentials);
      setCurrentUser(user);
      return user;
    } catch (err) {
      console.error('Erreur de connexion:', err);
      setError({
        code: err.code || 'auth_error',
        message: err.message || 'Erreur lors de la connexion'
      });
      throw err;
    } finally {
      setLoading(false);
    }
  };

  // Fonction de dconnexion
  const logout = async (revokeAll = false) => {
    setLoading(true);
    try {
      await enhancedAuthClient.logout(revokeAll);
      setCurrentUser(null);
      navigate('/login');
    } catch (err) {
      console.error('Erreur lors de la dconnexion:', err);
    } finally {
      setLoading(false);
    }
  };

  // Fonction pour obtenir un fetch authentifi
  const getAuthFetch = useCallback(async () => {
    try {
      return await enhancedAuthClient.getAuthenticatedFetch();
    } catch (err) {
      console.error('Erreur lors de la rcupration du fetch authentifi:', err);
      if (err.code === 'no_token') {
        setCurrentUser(null);
      }
      throw err;
    }
  }, []);

  // Fermeture du dialogue d'erreur
  const handleErrorClose = () => {
    setError(null);
  };

  // Gestionnaire d'action d'erreur
  const handleErrorAction = (action) => {
    setError(null);
    
    switch (action) {
      case 'login':
        navigate('/login');
        break;
      case 'manage_devices':
        navigate('/account/devices');
        break;
      default:
        // Autres actions
        break;
    }
  };

  // Valeurs exposes par le contexte
  const contextValue = {
    currentUser,
    loading,
    isAuthenticated: !!currentUser,
    login,
    logout,
    getAuthFetch,
    loadUserProfile,
    error
  };

  return (
    <AuthContext.Provider value={contextValue}>
      {children}
      
      {/* Gestionnaire d'erreurs d'authentification */}
      {error && (
        <AuthErrorHandler
          errorCode={error.code}
          errorMessage={error.message}
          open={!!error}
          onClose={handleErrorClose}
          onAction={handleErrorAction}
          useSnackbar={error.code === 'token_expired'} // Utiliser snackbar pour les erreurs moins critiques
        />
      )}
    </AuthContext.Provider>
  );
};

/**
 * Hook pour utiliser le contexte d'authentification
 * @returns {Object} Contexte d'authentification
 */
export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth doit tre utilis  l\'intrieur d\'un AuthProvider');
  }
  return context;
};

export default AuthContext;
